
.goods-list {
  float: left;
  width: 100%;
  padding: 0.26667rem 0.26667rem;
  background: #fff;
  box-sizing: border-box;
}
/* 商品列表-块级 */
.goods-grid {
  float: left;
  width: 45.3%;
  margin: 2%;
  border: 0.02667rem solid #f2f2f2;
}
.goods-grid a {
  float: left;
  color: #333;
}
.goods-grid .goods-icon {
  float: left;
  padding: 0.26667rem;
  height: 3.68rem;
  background: #f4f4f4;
}
.goods-grid .goods-icon img {
  float: left;
  width: 100%;
  height: 3.68rem;
}
.goods-grid .goods-detail {
  float: left;
  width: 100%;
  padding: 0.13333rem 0.26667rem;
  box-sizing: border-box;
}
.goods-grid .goods-title {
  float: left;
  width: 100%;
  height: 1.01333rem;
  font-size: 0.37333rem;
  display: -webkit-box;
/* autoprefixer: off */
  -webkit-box-orient: vertical;
/* autoprefixer: on */
  overflow: hidden;
  -webkit-line-clamp: 2;
}
/* autoprefixer: off */
.goods-grid .goods-price {
  float: left;
  margin-top: 0.05333rem;
  text-align: left;
  font-size: 0.37333rem;
  color: #3982f6;
}
.symbol {
  font-size: 0.21333rem;
}
.goods-grid .goods-sell {
  float: right;
  margin-top: 0.05333rem;
  text-align: right;
  font-size: 0.32rem;
  color: #999;
}
/* 商品列表-行级*/
.goods-grid-flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 0.26667rem 0;
  border-bottom: 0.02667rem solid #f2f2f2;
}
.goods-grid-flex:first-child {
  padding: 0 0 0.26667rem 0;
}
.goods-grid-flex:last-child {
  border-bottom: 0 solid #f2f2f2;
}
.goods-grid-flex .goods-icon {
  float: left;
  width: 34%;
}
.goods-grid-flex .goods-icon img {
  float: left;
  width: 100%;
  margin: 2.5%;
  height: 3.30667rem;
  border-radius: 0.13333rem;
}
.goods-grid-flex .goods-detail {
  float: left;
  width: 66%;
  padding: 0.13333rem 0.26667rem;
  box-sizing: border-box;
}
.goods-grid-flex .goods-title {
  float: left;
  width: 100%;
  height: 1.06667rem;
  font-size: 0.37333rem;
  color: #333;
  display: -webkit-box;
/* autoprefixer: off */
  -webkit-box-orient: vertical;
/* autoprefixer: on */
  overflow: hidden;
  -webkit-line-clamp: 2;
}
/* autoprefixer: off */
.goods-grid-flex .goods-detail .goods-option {
  float: left;
  margin-top: 1.33333rem;
  width: 100%;
}
.goods-grid-flex .goods-price {
  float: left;
  text-align: left;
  font-size: 0.37333rem;
  color: #3982f6;
}
.goods-grid-flex .goods-sell {
  float: right;
  text-align: right;
  font-size: 0.32rem;
  color: #aaa;
}


